import React, { useState, useEffect } from "react";
import bridge from "@vkontakte/vk-bridge";
import { navigate } from "@reach/router";
import { Tabs, Input, Button, Radio, Select } from "antd";
// import { Button } from "@vkontakte/vkui";
import cn from "classnames";

import "./TemplatePage.scss";

import { story } from "../../sharing-method";
import { nativeAds } from "../../ads";
import { addGroup, incrementCountButton } from "../../bridge-method";
import {
  GROUP_ID_MAILING_PAGE_1,
  APP_IMG_SHARING_STORIES,
  GROUP_ID_SUBSCRIPTION_PAGE_1,
  APP_ID_DEFAULT,
} from "../../constants";

import main from "../../img/bg/main.jpg";
import PageOne from "../../img/bg/page-one.jpg";

const images = {
  main: main,
  PageOne: PageOne,
};

const TemplatePage = ({
  icon,
  header,
  title,
  description,
  buttonName,
  setTemplatePage,
  name,
  next,
  fn,
  appID,
  getGroupId,
  openAlert,
  fetchedUser,
  getRandomImg,
  imgIndex,
  gotToken,
  publishStories,
  nameRadio,
  correctAnswer,
}) => {
  const [editTitle, setEditTitle] = useState(title);
  const [radioValue, setRadioValue] = useState("");

  console.log("nameRadio", nameRadio);

  function subscribeMessageFromGroupDefault(groupIDsubscription, type, next) {
    bridge
      .send("VKWebAppAllowMessagesFromGroup", {
        group_id: groupIDsubscription,
      })
      .then((res) => {
        if (type === "search") {
          setTemplatePage(next);
        }
        incrementCountButton(`stats.SquidGameAPP_${type}`);
      })
      .catch((err) => {
        bridge
          .send("VKWebAppAllowMessagesFromGroup", {
            group_id: groupIDsubscription,
          })
          .then((res) => {
            if (type === "search") {
              setTemplatePage(next);
            }
            incrementCountButton(`stats.SquidGameAPP_${type}`);
          })
          .catch((err) => {
            bridge
              .send("VKWebAppAllowMessagesFromGroup", {
                group_id: groupIDsubscription,
              })
              .then((res) => {
                if (type === "search") {
                  setTemplatePage(next);
                }
                incrementCountButton(`stats.SquidGameAPP_${type}`);
              })
              .catch((err) => {
                if (type === "search") {
                  setTemplatePage(next);
                }
              });
          });
      });
  }

  useEffect(() => {
    console.log("TemplatePage getGroupId", getGroupId);

    switch (name) {
      case "PageOne":
        // if (getGroupId.SquidGameAPPmsgPageOne) {
        //   subscribeMessageFromGroupDefault(
        //     getGroupId.SquidGameAPPmsgPageOne
        //       ? getGroupId.SquidGameAPPmsgPageOne
        //       : GROUP_ID_MAILING_PAGE_1,
        //     name,
        //     next
        //   );
        // }
        if (getGroupId.SquidGameAPPsubPageOne) {
          addGroup(
            getGroupId.SquidGameAPPsubPageOne
              ? getGroupId.SquidGameAPPsubPageOne
              : GROUP_ID_SUBSCRIPTION_PAGE_1,
            name
          );
        }

        break;
      case "like":
        // nativeAds();
        // addGroup(
        //   getGroupId.subLikePage
        //     ? getGroupId.subLikePage
        //     : GROUP_ID_SUBSCRIPTION_LIKE,
        //   name
        // );
        break;
      case "search":
        // const groupIdMan = getGroupId.msgSearchPageMan
        //   ? getGroupId.msgSearchPageMan
        //   : MAN_GROUP_ID_MAILING_SEARCH;

        // const groupIdWoman = getGroupId.subSearchPageWoman
        //   ? getGroupId.subSearchPageWoman
        //   : WOMAN_GROUP_ID_MAILING_SEARCH;

        // if (fetchedUser?.sex === 2) {
        //   setTimeout(() => {
        //     subscribeMessageFromGroupDefault(groupIdMan, name, next);
        //   }, 5000);
        // } else {
        //   setTimeout(() => {
        //     addGroup(groupIdWoman, name);
        //   }, 5000);
        // }

        break;
      case "result":
        // getRandomImg(0, 8);
        break;
      default:
        break;
    }
  }, []);

  const radioHandleChange = (e) => {
    console.log("radioHandleChange", e.target.value);
    console.log("correctAnswer", correctAnswer);

    setRadioValue(e.target.value);
    if (correctAnswer !== e.target.value) {
      setTemplatePage("main");
    } else {
      setTemplatePage(next);
    }
  };
  const questionPageBg = {
    background: `no-repeat url(${images[name]})`,
    backgroundSize: "cover",
    borderRadius: "10px",
    width: "100%",
    height: "300px",
  };
  const typeStyle = name === "main" ? null : questionPageBg;

  const questionPageBgMain = {
    background: `no-repeat url(${images[name]})`,
    backgroundSize: "cover",
  };

  const bg = {
    background: `linear-gradient(0deg,rgba(0, 0, 0, 0.910025706940874) 0%,rgba(0, 0, 0, 0.9922879177377892) 45%)`,
  };

  const typeStyleBg = name === "main" ? questionPageBgMain : bg;

  return (
    <div className="bg" style={typeStyleBg}>
      <div className="container">
        {/* <div className="container__icon">{icon}</div> */}
        {name !== "main" && (
          <div className="img">
            <img src={images[name]} alt="bg" />
          </div>
        )}

        <div className="container__main">
          <div className="header">{header}</div>
          <div className="title">{editTitle}</div>
          <div className="description">{description}</div>
        </div>

        {buttonName && (
          <div className="buttons">
            <Button
              onClick={() => {
                if (name === "result") {
                  story(APP_IMG_SHARING_STORIES[imgIndex], name);
                  // gotToken && publishStories();
                } else {
                  setTemplatePage(next);
                }
              }}
              className="buttons"
              stretched
            >
              <span>{buttonName}</span>
            </Button>
          </div>
        )}

        {nameRadio && nameRadio.length > 0 && (
          <div className="radio">
            <Radio.Group onChange={radioHandleChange} value={radioValue}>
              {nameRadio.map((item) => {
                return (
                  <Radio key={item} value={item}>
                    {item}
                  </Radio>
                );
              })}
            </Radio.Group>
          </div>
        )}
      </div>
    </div>
  );
};

export { TemplatePage };
